// 
export const getDateDiff = dateTimeStamp => {
  const minute = 1000 * 60
  const hour = minute * 60
  const day = hour * 24
  // const halfMonth = day * 15
  const month = day * 30

  const diffValue = Date.now() - dateTimeStamp

  let result = ''

  if (diffValue < 0) return result

  const monthC = diffValue / month
  const weekC = diffValue / (7 * day)
  const dayC = diffValue / day
  const hourC = diffValue / hour
  const minC = diffValue / minute

  if (monthC >= 1) {
    result = `${parseInt(monthC)}`
  } else if (weekC >= 1) {
    result = `${parseInt(weekC)}`
  } else if (dayC >= 1) {
    result = `${parseInt(dayC)}`
  } else if (hourC >= 1) {
    result = `${parseInt(hourC)}`
  } else if (minC >= 1) {
    result = `${parseInt(minC)}`
  } else {
    result = ''
  }

  return result
}
export function formatDate (date, fmt) {
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str))
    }
  }
  return fmt
}
function padLeftZero (str) {
  return ('00' + str).substr(str.length)
}



